body{
  background-image: url(/src/images/Large-Triangles.svg);
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  

}

#profile {
  height: 230px;
  width:200px;
  border-radius: 30px;
  padding-top: 20px;
  padding-bottom: 10px;
}

div #Profile-card {
  padding: 15px;
  border-radius: 15px;
 background-color: hsla(197, 53%, 52%, 0.8);
  color: white;
  font-size: 18px;
}

button{
  padding: 10px;
  margin-left: 30px;
  border-radius: 10px;
  border:none;
}

#likes {
  background-color: white;
  border-radius: 20px;
  height: 35px;
  width: 35px;
  margin: 5px;

}

span {
  font-family: sans-serif;
  display: block;
}

.Social-links{
  width: 35px;
  height: 35px;
  margin-left: 10px;
}

